@import '../mixin/link-btn';
@import '../mixin/scroll-area';

$black-font:#262626;
$gley-font: #88909b;

html{
  font-size: 10px;
  color:$black-font;
  font-family: 'Noto Sans JP', sans-serif;
}

a{
  display:inline-block;
  height:100%;
  width:100%;
}

img{
  width:100%;
  height:auto;
}

.sp{
  display: none;
}



body{
  font-size: 1.4rem;
  line-height: 2.2;
  position: relative;
  zoom:1;
  letter-spacing: 0.12em;
  min-width: 1280px;
}

.header{
  font-family: 'Barlow Condensed', sans-serif;
  position: relative;
  &__container{
    height: 162px;
    width: 100%;
    padding: 60px 9.15% 60px 4.4%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    z-index: 999;
  }
  &__logo{
    height: 100%;
    img{
      height: 100%;
      width: auto;
    }
  }
  &__menu{
    ul{
      padding-right: 50px;
    }
    li{
      float:left;
      display: list-item;
      line-height: 1.5;
      margin-left: 35px;
      font-size: 1.6rem;
      font-weight: 500;
    }

  }
  &__trigger{
    width: 50px;
    height: 50px;
    z-index: 20000;
    pointer-events: auto;
    cursor: pointer;
    position: fixed;
    right: 4.4%;
    top: 56px;
    transition:.3s;
    span{
      position: absolute;
      left: 5px;
      width: 40px;
      height: 1px;
      background: #333333;
    }
    span:nth-of-type(1){
      top:17px;
      animation: menuTrigger 4s infinite forwards;
    }
    span:nth-of-type(2){
      top:25px;
      animation: menuTrigger 4s infinite forwards .1s;
    }
    span:nth-of-type(3){
      bottom:16px;
      animation: menuTrigger 4s infinite forwards .2s;
    }
  }
  &__smenu{
    display: none;
    z-index: 10000;
    background-color:white;
    position: fixed;
    height: 100vh;
    width: 100vw;
    &--img{
      position: absolute;
      left:0;
      width: 50%;
      height: 100vh;
      overflow: hidden;
      display: flex;
      justify-content: center;
      img{
        height: 100%;
        width: auto;
      }
    }
    &--menu{
      position: absolute;
      right:0;
      width: 43%;
      height: 100vh;
      nav{
        line-height: 2;
        width: 50%;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        ul{
          li{
            font-size: 2.6rem;
            margin: 12px 0;
            letter-spacing: 0.35em;
            span{
              font-size: 1.4rem;
              margin-right: 15px;
              letter-spacing: 0.2em;
            }
          }
      }
      }
    }
    &--insta{
      width: 25px;
      height: 25px;
      display: block;
      width: 25px;
      position: absolute;
      right: 8.7%;
      bottom: 90px;
      opacity: 0.4;
    }
    &-list{
      font-weight: 300;
      position: absolute;
      right:8%;
      bottom:40px;
      li{
        float: left;
        margin-left: 20px;
        a{
          transition: all .2s;
        }
      }
      li.copyright{
        opacity: 0.4;
      }
    }
  }
}



#container{
  animation: fade 1.0s forwards;
}


.topview{
  position: relative;
  height: calc(130vh + 65px);
  //height: 100vh;
  min-height: 983px;
  &__title-area{
    position: absolute;
    top:36.5%;
    left:6.59%;
    transform:translateY(-50%);
  }
  &__name{
    font-family: 'Barlow Condensed', sans-serif;
    font-weight: 400;
    font-size: 1.5rem;
    letter-spacing: 0.55em;
    animation: fade 0.2s linear forwards 0.8s;
    opacity: 0;
  }
  &__copy{
    font-size: 8.2rem;
    font-family: 'Barlow Condensed', sans-serif;
    font-weight: 500;
    line-height: 1.45;
    letter-spacing: 0.85em;
    padding: 0 0 15px;
    &--wrapper{
      line-height: 1;
      margin: 0.42em 0;
      overflow: hidden;
    }
    &--1{
      animation: ttl-up 0.8s cubic-bezier(0.76, 0.09, 0.215, 1) forwards 0.4s;
      transform: translateY(80px);
      span{
        color: $gley-font;
      }
    }
    &--2{
      animation: ttl-up 0.8s cubic-bezier(0.76, 0.09, 0.215, 1) forwards 0.5s;
      transform: translateY(80px);
    }
  }
  &__maintxt{
    animation: fade 0.2s linear forwards 0.8s;
    font-size: 1.2rem;
    letter-spacing: 0.30em;
    font-weight: 400;
    color: #FFFFFF;
    opacity: 0;
  }
  &__visual-area{
    position: absolute;
    width:95.6%;
    top:36.5%;
    left:0;
    padding-top: 65px;
  }
  &__img{
    width: 100%;
    height: 80vh;
    overflow: hidden;
    display: flex;
    align-items: center;
    transition: .3s all;
  }
  .scroll{
    @include _scroll-area(36.5%, 9%);
  }
}


.about{
  position: relative;
  display: flex;
  align-items: center;
  padding: 120px 0 140px;
  &__video-area{
    width: 33%;
    height: 640px;
    margin-right: 10%;
    overflow: hidden;
    video{
      width: 100%;
    }
  }
  &__txt-area{
    width: 575px;
    padding: 60px 0;
    position: relative;
    font-size: 1.7rem;
    &::before{
      position: absolute;
      display: block;
      content:"";
      height: 1px;
      width: 30px;
      background-color: $black-font;
      left:0;
      top:0;
    }
    p{
      margin-bottom: 1.5em;
    }
  }
  &__title{
    position: absolute;
    top: 0;
    left: 100%;
    font-family: 'Barlow Condensed', sans-serif;
    font-weight: 600;
    font-size: 2.0rem;
    letter-spacing: 0.35em;
    transform: rotate(90deg);
    transform-origin: left top;
    line-height: 1;
    margin: 75px 0 0 75px;
  }
  &__btn{
    margin-top: 50px;
    margin-bottom: 0;
    .link-btn{
      @include _link-btn;
    }
  }
}


.works{
  position: relative;
  &__main{
    padding-top: 80px;
    align-items: center;
    display: flex;
    position: relative;
  }
  &__txtarea{
    width: 33%;
    box-sizing: border-box;
    padding-left: 6.588%;
    padding-bottom: 100px;
    position: relative;
    z-index: 1000;
  }
  &__title{
    position: relative;
    padding-bottom: 40px;
    padding-left: 5px;
    margin-bottom: 50px;
    span{
      position: relative;
      display: block;
      font-family: 'Barlow Condensed', sans-serif;
      font-weight: 600;
      letter-spacing: 0.55em;
      font-size: 2.8rem;
      line-height: 1.5;
    }
    &:after{
      position: absolute;
      content: "";
      width: 110%;
      height: 1px;
      background: #1c1f22;
      position: absolute;
      left: 0;
      bottom: 0;
    }
  }
  &__dl{
    font-family: 'Barlow Condensed', sans-serif;
    padding-left: 40px;
    margin-top: 20px;
    letter-spacing: 0.25em;
    line-height: 2;
    position: relative;
  }
  &__dt{
    font-size: 2.2rem;
    margin-bottom: 5px;
    letter-spacing: 0.25em;
    position: relative;
    &::before{
      position: absolute;
      content: "";
      width: 20px;
      height: 1px;
      background: #1c1f22;
      position: absolute;
      left: -40px;
      top: 50%;
      z-index: 1;
    }
  }
  &__dd{
    &--1, &--2{
      font-size: 1.2rem;
    }
    &--2{
      display: inline-block;
    }
  }
  &__link{
    margin-top: 20px;
    position: relative;
    display: inline-block;
    font-weight: 600;
    padding-right: 40px;
    &::after{
      position: relative;
      content: "";
      width: 20px;
      height: 1px;
      background: #1c1f22;
      position: absolute;
      right: 0;
      top: 50%;
    }
  }
  &__img{
    width: 67%;
    height: 576px;
    overflow: hidden;
    display: flex;
    align-items: center;
    position: relative;
    &::before{
      position: absolute;
      content: "";
      width: 100%;
      height: 100%;
      background: #88909b;
      position: absolute;
      right: 0;
      top: 0;
      transform-origin: right center;
      z-index: 1;
      animation: ef-mask 0.85s cubic-bezier(0.76, 0.09, 0.215, 1) forwards;
    }
  }
  &__list{
    padding: 0 0 100px 6.6%;
    position: relative;
    &::before{
      position: absolute;
      height: calc(100% + 140px);
      top: -140px;
      background: rgba(245,246,247,0.80);
      mix-blend-mode: multiply;
      z-index: 1;
    }
  }
  &__slider{
    position: relative;
    z-index: 2;
    display: block;
    user-select: none;
    touch-action: pan-y;
    .slick-prev, .slick-next{
      position: absolute;
      top: -50px;
      width: 34px;
      height: 27px;
      display: block;
      cursor: pointer;
      background-position: center center;
      background-repeat: no-repeat;
      background-size: 14px auto;
      background-color: transparent;
      z-index: 999;
    }
    .slick-prev{
      background-image: url(../img/arrow_prev.svg);
      left:0px;
    }
    .slick-next{
      background-image: url(../img/arrow_next.svg);
      left:65px;
    }
    .work{
      &__item{
      margin-top: 50px;
      margin-left: 50px;
      float:left;
      height: 100%;
      position: relative;
      }
      &__thumb{
        width: 480px;
        position: relative;
      }
      &__num{
        position: absolute;
        display: block;
        line-height: 1;
        top:15px;
        left: -25px;
        transform: rotate(90deg);
        transform-origin: left top;
        padding-left: 35px;
        color:white;
        &::before{
          position: absolute;
          content: "";
          width: 20px;
          height: 1px;
          background: #FFFFFF;
          left: 0;
        }
      }
      &__title{
        font-family: 'Barlow Condensed', sans-serif;
        padding-left: 40px;
        margin-top: 20px;
        letter-spacing: 0.25em;
        line-height: 2;
        dt{
          font-size: 1.8rem;
          font-weight: 600;
          position: relative;
          &::before{
            content: "";
            width: 20px;
            height: 1px;
            background: #1c1f22;
            position: absolute;
            left: -40px;
            top: 50%;
          }
        }
        dd{
          font-size: 1.2rem;
        }
      }
    }
  }
  &__btn{
    text-align: right;
    padding-right: 6.6%;
    position: relative;
    margin-top: 50px;
    .link-btn{
      @include _link-btn;
    }
  }
}



.service{
