html {
  font-size: 10px;
  color: #262626;
  font-family: 'Noto Sans JP', sans-serif;
}

a {
  display: inline-block;
  height: 100%;
  width: 100%;
}

img {
  width: 100%;
  height: auto;
}

.sp {
  display: none;
}

body {
  font-size: 1.4rem;
  line-height: 2.2;
  position: relative;
  zoom: 1;
  letter-spacing: 0.12em;
  min-width: 1280px;
}

.header {
  font-family: 'Barlow Condensed', sans-serif;
  position: relative;
}

.header__container {
  height: 162px;
  width: 100%;
  padding: 60px 9.15% 60px 4.4%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  z-index: 999;
}

.header__logo {
  height: 100%;
}

.header__logo img {
  height: 100%;
  width: auto;
}

.header__menu ul {
  padding-right: 50px;
}

.header__menu li {
  float: left;
  display: list-item;
  line-height: 1.5;
  margin-left: 35px;
  font-size: 1.6rem;
  font-weight: 500;
}

.header__trigger {
  width: 50px;
  height: 50px;
  z-index: 20000;
  pointer-events: auto;
  cursor: pointer;
  position: fixed;
  right: 4.4%;
  top: 56px;
  transition: .3s;
}

.header__trigger span {
  position: absolute;
  left: 5px;
  width: 40px;
  height: 1px;
  background: #333333;
}

.header__trigger span:nth-of-type(1) {
  top: 17px;
  animation: menuTrigger 4s infinite forwards;
}

.header__trigger span:nth-of-type(2) {
  top: 25px;
  animation: menuTrigger 4s infinite forwards .1s;
}

.header__trigger span:nth-of-type(3) {
  bottom: 16px;
  animation: menuTrigger 4s infinite forwards .2s;
}

.header__smenu {
  display: none;
  z-index: 10000;
  background-color: white;
  position: fixed;
  height: 100vh;
  width: 100vw;
}

.header__smenu--img {
  position: absolute;
  left: 0;
  width: 50%;
  height: 100vh;
  overflow: hidden;
  display: flex;
  justify-content: center;
}

.header__smenu--img img {
  height: 100%;
  width: auto;
}

.header__smenu--menu {
  position: absolute;
  right: 0;
  width: 43%;
  height: 100vh;
}

.header__smenu--menu nav {
  line-height: 2;
  width: 50%;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.header__smenu--menu nav ul li {
  font-size: 2.6rem;
  margin: 12px 0;
  letter-spacing: 0.35em;
}

.header__smenu--menu nav ul li span {
  font-size: 1.4rem;
  margin-right: 15px;
  letter-spacing: 0.2em;
}

.header__smenu--insta {
  width: 25px;
  height: 25px;
  display: block;
  width: 25px;
  position: absolute;
  right: 8.7%;
  bottom: 90px;
  opacity: 0.4;
}

.header__smenu-list {
  font-weight: 300;
  position: absolute;
  right: 8%;
  bottom: 40px;
}

.header__smenu-list li {
  float: left;
  margin-left: 20px;
}

.header__smenu-list li a {
  transition: all .2s;
}

.header__smenu-list li.copyright {
  opacity: 0.4;
}

#container {
  animation: fade 1.0s forwards;
}

.topview {
  position: relative;
  height: calc(130vh + 65px);
  min-height: 983px;
}

.topview__title-area {
  position: absolute;
  top: 36.5%;
  left: 6.59%;
  transform: translateY(-50%);
}

.topview__name {
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 400;
  font-size: 1.5rem;
  letter-spacing: 0.55em;
  animation: fade 0.2s linear forwards 0.8s;
  opacity: 0;
}

.topview__copy {
  font-size: 8.2rem;
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 500;
  line-height: 1.45;
  letter-spacing: 0.85em;
  padding: 0 0 15px;
}

.topview__copy--wrapper {
  line-height: 1;
  margin: 0.42em 0;
  overflow: hidden;
}

.topview__copy--1 {
  animation: ttl-up 0.8s cubic-bezier(0.76, 0.09, 0.215, 1) forwards 0.4s;
  transform: translateY(80px);
}

.topview__copy--1 span {
  color: #88909b;
}

.topview__copy--2 {
  animation: ttl-up 0.8s cubic-bezier(0.76, 0.09, 0.215, 1) forwards 0.5s;
  transform: translateY(80px);
}

.topview__maintxt {
  animation: fade 0.2s linear forwards 0.8s;
  font-size: 1.2rem;
  letter-spacing: 0.30em;
  font-weight: 400;
  color: #FFFFFF;
  opacity: 0;
}

.topview__visual-area {
  position: absolute;
  width: 95.6%;
  top: 36.5%;
  left: 0;
  padding-top: 65px;
}

.topview__img {
  width: 100%;
  height: 80vh;
  overflow: hidden;
  display: flex;
  align-items: center;
  transition: .3s all;
}

.topview .scroll {
  width: 12px;
  height: 300px;
  position: absolute;
  top: 36.5%;
  right: 9%;
  transform: translateY(-45%);
}

.topview .scroll p {
  position: absolute;
  top: 0;
  left: 12px;
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 500;
  font-size: 1.2rem;
  letter-spacing: 0.3em;
  transform: rotate(90deg);
  transform-origin: left top;
  line-height: 1;
  z-index: 9999;
}

.topview .scroll__bar--1, .topview .scroll__bar--2 {
  display: block;
  position: absolute;
  bottom: 0px;
  left: 6px;
  width: 1px;
  height: 230px;
  background: rgba(51, 51, 51, 0.1);
  pointer-events: none;
}

.topview .scroll__bar--2 {
  background: rgba(51, 51, 51, 0.4);
  transform: scaleY(0);
  animation: scroll-bar 3s ease-in-out infinite forwards;
}

.about {
  position: relative;
  display: flex;
  align-items: center;
  padding: 120px 0 140px;
}

.about__video-area {
  width: 33%;
  height: 640px;
  margin-right: 10%;
  overflow: hidden;
}

.about__video-area video {
  width: 100%;
}

.about__txt-area {
  width: 575px;
  padding: 60px 0;
  position: relative;
  font-size: 1.7rem;
}

.about__txt-area::before {
  position: absolute;
  display: block;
  content: "";
  height: 1px;
  width: 30px;
  background-color: #262626;
  left: 0;
  top: 0;
}

.about__txt-area p {
  margin-bottom: 1.5em;
}

.about__title {
  position: absolute;
  top: 0;
  left: 100%;
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 600;
  font-size: 2.0rem;
  letter-spacing: 0.35em;
  transform: rotate(90deg);
  transform-origin: left top;
  line-height: 1;
  margin: 75px 0 0 75px;
}

.about__btn {
  margin-top: 50px;
  margin-bottom: 0;
}

.about__btn .link-btn {
  display: inline-block;
  width: 360px;
  line-height: 65px;
  text-align: right;
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 600;
  font-size: 1.4rem;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  background: #1c1f22;
  color: #FFFFFF;
  letter-spacing: 0.2em;
  box-sizing: border-box;
  transition: .2s;
}

.about__btn .link-btn-line {
  display: block;
  width: 30px;
  height: 1px;
  background: #FFFFFF;
  position: absolute;
  right: 45px;
  top: 50%;
  transition: .2s;
}

.about__btn .link-btn::before {
  position: absolute;
  display: block;
  content: "";
  width: 0;
  height: 1px;
  background-color: #1c1f22;
  transition: all .2s ease-out;
}

.about__btn .link-btn::after {
  position: absolute;
  display: block;
  content: "";
  width: 0;
  height: 1px;
  background-color: #1c1f22;
  transition: all .2s ease-out;
  right: 0;
  bottom: 0;
}

.about__btn .link-btn-bd {
  display: inline-block;
  width: 100%;
  height: 100%;
  padding-right: 95px;
  transition: .2s;
}

.about__btn .link-btn-bd:hover {
  padding-right: 115px;
}

.about__btn .link-btn-bd::before {
  position: absolute;
  display: block;
  content: "";
  height: 0;
  width: 1px;
  background-color: #1c1f22;
  transition: all .2s ease-out;
  left: 0;
  bottom: 0;
}

.about__btn .link-btn-bd::after {
  position: absolute;
  display: block;
  content: "";
  height: 0;
  width: 1px;
  background-color: #1c1f22;
  transition: all .2s ease-out;
  top: 0;
  right: 0;
}

.about__btn .link-btn:hover {
  background: rgba(28, 31, 34, 0);
  color: #262626;
  opacity: 1;
}

.about__btn .link-btn:hover .link-btn-line {
  width: 50px;
  background: #1c1f22;
}

.about__btn .link-btn:hover::before {
  width: 100%;
  transition-delay: .1s;
}

.about__btn .link-btn:hover::after {
  width: 100%;
  transition-delay: .3s;
}

.about__btn .link-btn:hover .link-btn-bd::before {
  height: 100%;
  transition-delay: .4s;
}

.about__btn .link-btn:hover .link-btn-bd::after {
  height: 100%;
  transition-delay: .2s;
}

