  top: 50%;
}

.works__img {
  width: 67%;
  height: 576px;
  overflow: hidden;
  display: flex;
  align-items: center;
  position: relative;
}

.works__img::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background: #88909b;
  position: absolute;
  right: 0;
  top: 0;
  transform-origin: right center;
  z-index: 1;
