  z-index: 1;
}

.works__slider {
  position: relative;
  z-index: 2;
  display: block;
  user-select: none;
  touch-action: pan-y;
}

.works__slider .slick-prev, .works__slider .slick-next {
  position: absolute;
  top: -50px;
  width: 34px;
  height: 27px;
  display: block;
  cursor: pointer;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 14px auto;
  background-color: transparent;
  z-index: 999;
}

.works__slider .slick-prev {
  background-image: url(../img/arrow_prev.svg);
