.news {
  padding: 0 6.6%;
}

.news__container {
  max-width: 1680px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  padding: 120px 0 80px;
  position: relative;
}

.news__titlearea {
  padding-top: 80px;
}

.news__title {
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 600;
  letter-spacing: 0.55em;
