@keyframes menuTrigger {
  0% {
    transform-origin: top left;
    transform: scaleX(0);
  }
  10% {
    transform-origin: top left;
    transform: scaleX(1);
  }
  85% {
    transform-origin: top right;
    transform: scaleX(1);
  }
  95% {
    transform-origin: top right;
    transform: scaleX(0);
  }
  100% {
    transform-origin: top left;
    transform: scaleX(0);
  }
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes ttl-up {
  0% {
    transform: translateY(80px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes scroll-bar {
  0% {
    transform-origin: top left;
    transform: scaleY(0);
  }
  50% {
    transform-origin: top left;
    transform: scaleY(1);
  }
  75% {
    transform-origin: bottom left;
    transform: scaleY(1);
  }
  95% {
    transform-origin: bottom left;
    transform: scaleY(0);
  }
  100% {
    transform-origin: top left;
    transform: scaleY(0);
  }
}

@keyframes ef-mask {
  0% {
    right: 0;
    transform: scaleX(1);
  }
  99% {
    right: -5px;
    opacity: 1;
    transform: scaleX(0);
  }
  100% {
    opacity: 0;
    transform: scaleX(0);
  }
}
